    :style="{ width: '450px' }"
    header="Confirm"
    :modal="true"
>
<div class="flex align-items-center justify-content-center">
    <i
        class="pi pi-exclamation-triangle mr-3"
        style="font-size: 2rem"
    />
    <span v-if="post"
    >Are you sure you want to delete <b>{{ post.name }}</b
    >?</span
    >
</div>
<template #footer>
    <Button
        label="No"
        icon="pi pi-times"
        class="p-button-text"
        @click="deletePostDialog = false"
    />
    <Button
        label="Yes"
        icon="pi pi-check"
        class="p-button-text"
        @click="deletePost"
    />
</template>
</Dialog>

<script>
import { FilterMatchMode } from "primevue/api";
import { useToast } from "primevue/usetoast";
import {data} from "autoprefixer";

export default {
    computed: {
        data() {
            return data
        }
    },
    props: {
        currentPosts: {
            type: Array,
            required: true,
        },

    }, //end of props

    emits: ["selectPosts", "deletePost", "editPost"],

    data() {
        return {
            layout: 'grid',
            toast: null,
            loading: false,
            postDialog: false,
            deletePostDialog: false,
            post: {},
            posts: this.currentPosts,
            selectedPosts: null,
            filters: {
                global: { value: null, matchMode: FilterMatchMode.CONTAINS },
            },
        };
    }, //end of data

    watch: {
        selectedPosts(val) {
            this.$emit("selectPosts", val);
        },
    }, //end of watch

    beforeMount() {
        this.initFilters();
        this.toast = useToast();
    }, //end of beforeMount
    methods: {
        confirmDeletePost(post) {
            this.post = post;
            this.deletePostDialog = true;
        }, //end of confirmDeletePost

        deletePost() {
            this.loading = true;
            axios
                .delete("/api/admin/posts/" + this.post.id)
                .then((response) => {
                    this.toast.add({
                        severity: "success",
                        summary: "Successful",
                        detail: response.data.message,
                        life: 3000,
                    });
                    this.$emit("deletePost");
                    this.deletePostDialog = false;
                    this.post = {};
                })
                .catch((errors) => {
                    if (errors.response) {
                        this.toast.add({
                            severity: "error",
                            summary: "Error",
                            detail: errors.response.data.message,
                            life: 15000,
                        });
                    }
                })
                .then(() => {
                    this.loading = false;
                    this.deletePostDialog = false;
                });
        }, //end of deletePost

        initFilters() {
            this.filters = {
                global: { value: null, matchMode: FilterMatchMode.CONTAINS },
            };
        }, //end of initFilters

        exportCSV() {
            this.$refs.dt.exportCSV();
        }, //end of exportCSV

        editPost(post) {
            this.$emit("editPost", post);
        }, //end of editPost
    }, //end of methods
};
</script>

